import { storage } from '../../utils/storage'

const state = {
  allData: null
}

const mutations = {
  setData (state, data) {
    state.allData = data
  }
}

const getters = {
  appData (state) {
    return state.allData
  }
}

const actions = {
  setData (context, data) {
    storage.storeValue('events', data)
    context.commit('setData', {state, data})
  }
}

export default {
  state,
  actions,
  mutations,
  getters
}



